<template>
  <InputBase :class="{ disabled: isDisabled, tf_error: isError, tf_border: isBorderStyle }">
    <input 
      v-model="input" 
      :placeholder="placeholder" 
      :disabled="isDisabled"
      :maxLength="maxLength"
      @focus="onFocus"
      @blur="onBlur"
      ref="input"
      KeyboardEvent.key:keyup.13="onKeyUpEnter" />
      <!-- type="number"  -->
  </InputBase>
</template>

<script>
import InputBase from '@/components/common/input/InputBase'
import { getCanConvertEngNumber } from '@/utils/stringUtils'
// import { addCommaForMoney } from '~/utils/stringUtils'

export default {
  name: 'InputEngNumber',
  components: {
    InputBase
  },
  props: {
    value: String,
    placeholder: String,
    isBorderStyle:{
      type: Boolean,
      default: false
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxLength: Number,
    isFocusOnMount: {
      type: Boolean,
      default: false
    },
  },
  mounted() {
    if( this.isFocusOnMount ) {
      this.$refs.input.focus();
    }
  },
  computed: {
    input: {
      get() {
        const validText = this.getValidText( this.value );

        if( validText !== this.value ) {
          this.$emit("update:value", validText );
        }
        return this.value;
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  methods: {
    getValidText( strOrg ) {
      if( !strOrg ) return '';

      strOrg = strOrg.trim();
      
      // 그냥 다 영/숫자면 그대로 리턴.
      if( getCanConvertEngNumber( strOrg ) ) {
        return strOrg;
      }

      let ret = '';

      let char;

      for(var i=0; i<strOrg.length; ++i) {
        char = strOrg.charAt( i );

        if( !getCanConvertEngNumber( char ) )  continue;

        ret += char;
      }

      if( !ret )  return '';
      return ret;
    },
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter() {
      this.blur();
    }
  }
}
</script>