<template>
  <Popup
    :isBorder="false"
    :isHeightFit="true"
    :isLayer="true"
    maxWidth="400px"
    title="비밀번호 찾기"
    :closeBtnText="viewModel.isSendedFind ? '닫기' : '취소'"
    :compeleteBtnText="viewModel.isSendedFind ? '':'비밀번호 찾기'"
    @onClickClose="viewModel.onClickClosePwFindPopup()"
    @onClickComplete="viewModel.onClickCompletePwFindPopup()">
    <p class="desc_layer" v-html="viewModel.isSendedFind ? '입력하신 E-mail로<br>임시비밀번호 전송이 완료되었습니다.' : '아이디와 E-mail를 입력해주시면<br>임시비밀번호를 문자로 보내드립니다.'"></p>
    <dl class="list_from_s">
      <dt>이름</dt>
      <dd>
        <InputEngNumber
          :isBorderStyle="true"
          :maxLength="20"
          placeholder="이름을 입력해주세요"
          :isFocusOnMount="true"
          :isError="viewModel.isErrorFindName"
          :isDisabled="viewModel.isSendedFind"
          v-model:value="viewModel.model.findName"/>
      </dd>
      <dt>아이디</dt>
      <dd>
        <InputEngNumber
          :isBorderStyle="true"
          :maxLength="20"
          placeholder="아이디 (가입하신 E-mail을 입력해주세요)"
          :isError="viewModel.isErrorFindEmail"
          :isDisabled="viewModel.isSendedFind"
          v-model:value="viewModel.model.findEmail"/>
      </dd>
      <dt>휴대폰번호</dt>
      <dd>
        <InputTel
          :isBorderStyle="true"
          placeholder="- 없이 입력"
          v-model:value="viewModel.model.findPhone"
          :isError="viewModel.isErrorFindPhone"
          :isDisabled="viewModel.isSendedFind" />
        <p v-if="viewModel.errorMessagePopup" class="desc_error"><span class="ico_zippro ico_exclam_r"></span>{{ viewModel.errorMessagePopup }}</p>
      </dd>
    </dl>
  </Popup>
</template>

<script>
import Popup from '@/components/layout/popup/Popup';
import InputEngNumber from '@/components/common/input/InputEngNumber';
import Input from '@/components/common/input/Input';
import InputTel from '@/components/common/input/InputTel';

export default {
  name: 'PwFindPopup',
  components:{
    Popup,
    InputEngNumber,
    Input,
    InputTel
  },
  props:{
    viewModel: Object
  },
}
</script>

<style scoped>
.desc_layer{font-size:14px;line-height:20px;color:#555;letter-spacing:-0.02em}
.list_from_s{margin-top:20px}
.desc_error{padding:10px 0 0;font-size:12px;line-height:14px;color:#F8626C}
.desc_error .ico_exclam_r{margin:1px 4px 1px 0}
</style>