import LoginModel from '@/views/login/model/LoginModel';

export default class LoginViewModel {
  constructor() {
    this.model = {};
    // 로그인 - 에러
    this.isErrorId = false;
    this.isErrorPW = false;
    this.errorMessage = '';

    // 아이디,비밀번호찾기 팝업
    this.isIdFindPopup = false;
    this.isPwFindPopup = false;

    // 아이디,비밀번호찾기 팝업 - 전송완료여부
    this.isSendedFind = false;
    // 아이디,비밀번호찾기 팝업 - 에러
    this.isErrorFindEmail = false;
    this.isErrorFindName = false;
    this.isErrorFindPhone = false;
    this.errorMessagePopup = '';

    // 소셜로그인유형
    this.socialDataList = [
      { id: 'naver', btnStyle: 'naver', text: '네이버 계정으로 로그인', isOauth: true },
      { id: 'kakao', btnStyle: 'kakao', text: '카카오 계정으로 로그인', isOauth: true },
      { id: 'goole', btnStyle: 'goole', text: 'Google 계정으로 로그인', isOauth: true },
      { id: 'idpw', btnStyle: 'tertiary', text: '회원가입', isOauth: false },
    ];
  }

  setModel(){
    this.model = new LoginModel();
  }

  // 로그인
  onClickLogin(){
    this.isErrorId = !this.model.id;
    this.isErrorPW = !this.model.password;
    if(this.isErrorId || this.isErrorPW){
      this.errorMessage = '아이디 또는 비밀번호가 정확하지 않습니다.'
    }else{
      // 로그인 API 전송
      // 성공 : 메인화면으로 라우팅
      // 실패 : 에러문구 적용
      console.log('login');
      this.errorMessage = '';
    }
  }

  // 아이디찾기 팝업 열기
  onClickIdFindPopup(){
    this.isIdFindPopup = true;
  }
  // 아이디찾기 팝업 닫기
  onClickCloseIdFindPopup(){
    this.isIdFindPopup = false;
    // 입력한 휴대폰번호 리셋
    this.model.findName = '';
    this.model.findPhone = '';
    // 에러, 전송완료여부 리셋
    this.isErrorFindPhone = false;
    this.errorMessagePopup = false;
    this.isSendedFind = false;
  }
  // 아이디찾기 팝업 전송
  onClickCompleteIdFindPopup(){
    this.isErrorFindName = !this.model.findName;
    this.isErrorFindPhone = !this.model.findPhone;
    if(!this.model.findName || !this.model.findPhone){
      this.isErrorFindPhone = true;
      this.errorMessagePopup = '이름 또는 휴대폰번호가 정확하지 않습니다.';
    }else{
      // 아이디찾기 문자전송 API
      // 성공 : 에러 리셋, 성공처리
      // 실패 : 에러문구 적용
      this.model.id = 'kerryZippro@gmail.com'; // 임시 결과값
      this.isErrorFindPhone = false;
      this.errorMessagePopup = false;
      this.isSendedFind = true;
    }
  }

  // 비밀번호찾기 팝업 열기
  onClickPwFindPopup(){
    this.isPwFindPopup = true;
  }
  // 비밀번호찾기 팝업 닫기
  onClickClosePwFindPopup(){
    this.isPwFindPopup = false;
    // 입력한 아이디, 휴대폰번호 리셋
    this.model.findName = '';
    this.model.findEmail = '';
    this.model.findPhone = '';
    // 에러, 전송완료여부 리셋
    this.isErrorFindName = false;
    this.isErrorFindEmail = false;
    this.isErrorFindPhone = false;
    this.errorMessagePopup = false;
    this.isSendedFind = false;
  }
  // 비밀번호찾기 팝업 전송
  onClickCompletePwFindPopup(){
    this.isErrorFindName = !this.model.findName;
    this.isErrorFindEmail = !this.model.findEmail;
    this.isErrorFindPhone = !this.model.findPhone;
    if(!this.model.findName || !this.model.findEmail || !this.model.findPhone){
      this.errorMessagePopup = '이름, 아이디 또는 휴대폰번호가 정확하지 않습니다.';
    }else{
      // 비밀번호찾기 문자전송 API
      // 성공 : 에러 리셋, 성공처리
      // 실패 : 에러문구 적용
      this.isErrorFindName = false;
      this.isErrorFindEmail = false;
      this.isErrorFindPhone = false;
      this.errorMessagePopup = false;
      this.isSendedFind = true;
    }
  }

  // 소셜로그인유형 - 유형 선택시
  onClickSocail (seletedSocialData ){
    if(seletedSocialData.isOauth){
      this.OauthSignUp(seletedSocialData.id);
    }
  }
  // OAuth 로그인 Redirect
  OauthSignUp(OauthType){
    console.log(OauthType,'API Response URL Redirect');
  }
}