<template>
  <PageWithLayout
    :isMiddleMode="true">
    <Input
      :isBorderStyle="true"
      placeholder="아이디"
      :isFocusOnMount="true"
      :isError="viewModel.isErrorId"
      v-model:value="viewModel.model.id"/>
    <Input
      type="password"
      :isBorderStyle="true"
      placeholder="비밀번호"
      :isError="viewModel.isErrorPW"
      v-model:value="viewModel.model.password" />
    <p v-if="viewModel.errorMessage" class="desc_error"><span class="ico_zippro ico_exclam_r"></span>{{ viewModel.errorMessage }}</p>
    <Button
      btnSize="xlarge"
      btnStyle="secondary"
      text="로그인"
      @onClickBtn="viewModel.onClickLogin()" />

    <div class="util_login">
      <button
        class="btn_util"
        @click="viewModel.onClickIdFindPopup()">
        아이디 찾기
      </button>
      <span class="txt_bar"></span>
      <button
        class="btn_util"
        @click="viewModel.onClickPwFindPopup()">
        비밀번호 찾기
      </button>
    </div>

    <strong class="tit_social">다른 방법으로 로그인</strong>
    <template
      v-for="socialData in viewModel.socialDataList"
      :key="socialData.id">
      <strong v-if="!socialData.isOauth" class="tit_social">아직 회원이 아니시라면</strong>
      <Button
        btnSize="xlarge"
        :btnStyle="socialData.btnStyle"
        :text="socialData.text"
        :routePath="!socialData.isOauth ? 'SIGNUP' : ''"
        @onClickBtn="viewModel.onClickSocail(socialData)" />
    </template>

    <template v-slot:popup>
      <IdFindPopup
        v-if="viewModel.isIdFindPopup"
        :viewModel="viewModel"/>
      <PwFindPopup
        v-if="viewModel.isPwFindPopup"
        :viewModel="viewModel"/>
    </template>
  </PageWithLayout>
</template>

<script>
import PageWithLayout from '@/components/layout/PageWithLayout'
import InputEngNumber from '@/components/common/input/InputEngNumber';
import Input from '@/components/common/input/Input';
import Button from '@/components/common/button/Button';
// popup
import IdFindPopup from '@/views/login/view/popup/IdFindPopup';
import PwFindPopup from '@/views/login/view/popup/PwFindPopup';
// viewModel
import LoginViewModel from '@/views/login/viewModel/LoginViewModel';

export default {
  name:'login',
  components:{
    PageWithLayout,
    InputEngNumber,
    Input,
    Button,

    IdFindPopup,
    PwFindPopup
  },
  beforeMount(){
    this.viewModel.setModel();
  },
  data(){
    return{
      viewModel: new LoginViewModel(),
    }
  },
}
</script>
<style scoped>
.tf_comm{height:56px;padding:0 10px 1px;font-size:16px}
.tf_error{padding-bottom:0px}

.tf_comm + .tf_comm{margin-top:10px}
.tf_comm + .btn_xlarge{margin-top:20px}

.util_login{margin-top:10px}
.btn_util{width:calc(50% - 0.5px);padding:10px 0;font-size:14px;line-height:18px;color:#888}
.txt_bar{display:inline-block;width:1px;height:18px;margin:10px 0;background-color:#e1e1e1;vertical-align:top}

.tit_social{display:block;margin:30px 0 10px;font-size:14px;color:#555}
</style>