<template>
  <Popup
    :isBorder="false"
    :isHeightFit="true"
    :isLayer="true"
    maxWidth="400px"
    title="아이디 찾기"
    :closeBtnText="viewModel.isSendedFind ? '로그인하러 가기' : '취소'"
    :compeleteBtnText="viewModel.isSendedFind ? '':'아이디 찾기'"
    @onClickClose="viewModel.onClickCloseIdFindPopup()"
    @onClickComplete="viewModel.onClickCompleteIdFindPopup()">
    <p class="desc_layer" v-if="viewModel.isSendedFind">회원님의 정보와 일치하는 아이디입니다.</p>
    <dl v-if="!viewModel.isSendedFind" class="list_from_s">
      <dt>이름</dt>
      <dd>
        <Input
          :isBorderStyle="true"
          placeholder="이름을 입력해주세요"
          v-model:value="viewModel.model.findName"
          :isFocusOnMount="true"
          :isError="viewModel.isErrorFindName"
          :isDisabled="viewModel.isSendedFind" />
      </dd>
      <dt>휴대폰번호</dt>
      <dd>
        <InputTel
          :isBorderStyle="true"
          placeholder="- 없이 입력"
          v-model:value="viewModel.model.findPhone"
          :isFocusOnMount="true"
          :isError="viewModel.isErrorFindPhone"
          :isDisabled="viewModel.isSendedFind" />
      </dd>
    </dl>
    <strong v-else class="txt_result">{{ viewModel.model.id }}</strong>
    <p v-if="viewModel.errorMessagePopup" class="desc_error"><span class="ico_zippro ico_exclam_r"></span>{{ viewModel.errorMessagePopup }}</p>
  </Popup>
</template>

<script>
import Popup from '@/components/layout/popup/Popup';
import Input from '@/components/common/input/Input';
import InputTel from '@/components/common/input/InputTel';

export default {
  name: 'idFindPopup',
  components:{
    Popup,
    Input,
    InputTel
  },
  props:{
    viewModel: Object
  },
}
</script>

<style scoped>
.desc_layer{font-size:14px;line-height:18px;color:#555;letter-spacing:-0.02em}
.desc_layer:deep() .ico_exclam{margin:3px 4px 3px 0}
.list_from_s{margin-top:20px}
.desc_error{padding:10px 0 0;font-size:12px;line-height:14px;color:#F8626C}
.desc_error .ico_exclam_r{margin:1px 4px 1px 0}

.txt_result{display:block;margin-top:10px;font-size:16px;line-height:18px;color:#222}
</style>